<script type="text/jsx">
import Vue from 'vue'
import CButton from '../button/button'
const Notification = Vue.extend({
  components: {
    CButton
  },
  data () {
    return {
      className: '', // class
      width: 255,
      content: 'content', // 
      duration: 4.5, // ,4.5
      icon: '', // 
      title: 'title', // 
      placement: 'topRight', //  topLeft topRight bottomLeft bottomRight
      onClose: () => {} // 
    }
  },
  computed: {
    clazz () {
      let clazz = {
        'c-notification': true
      }
      if (this.className) clazz[this.className] = true
      return clazz
    },
    style () {
      return {
        width: this.width + 'px'
      }
    }
  },
  methods: {
    close () {
      this.$destroy()
      this.$el.remove()
      this.onClose()
    },
    timeout () {
      setTimeout(() => {
        this.close()
      }, this.duration * 1000)
    }
  },
  mounted () {
    if (this.duration) {
      this.timeout()
    }
  },
  render (h) {
    return (
      <div class={this.clazz} style={this.style}>
        <div class="c-notification_">
          {(() => {
            if (this.icon) {
              return (
                <i class={this.icon}></i>
              )
            }
          })()}
          <div class="c-notification-center">
            <div class="c-notification-title">{this.title}</div>
            <div class="c-notification-content" domPropsInnerHTML={this.content}></div>
          </div>
        </div>
        <c-button onClick={this.close} type="primary"></c-button>
      </div>
    )
  }
})

export default Notification
</script>

<style lang="sass">
@import "styles/index"
</style>



